import { mapActions, mapGetters } from 'vuex';
import MapMarker from 'vue-material-design-icons/MapMarker';
import Plus from 'vue-material-design-icons/Plus';
import Delete from 'vue-material-design-icons/Delete';
import ImagePlus from 'vue-material-design-icons/ImagePlus';
import Close from 'vue-material-design-icons/Close'; // import Download from 'vue-material-design-icons/Download'
// import DownloadMultiple from 'vue-material-design-icons/DownloadMultiple'

import { NcActions, NcActionButton, NcButton, NcModal, NcEmptyContent, NcActionSeparator, NcUserBubble, isMobile } from '@nextcloud/vue';
import { getCurrentUser } from '@nextcloud/auth';
import FetchSharedAlbumsMixin from '../mixins/FetchSharedAlbumsMixin.js';
import FetchFilesMixin from '../mixins/FetchFilesMixin.js';
import AbortControllerMixin from '../mixins/AbortControllerMixin.js';
import CollectionContent from '../components/Collection/CollectionContent.vue';
import HeaderNavigation from '../components/HeaderNavigation.vue'; // import ActionDownload from '../components/Actions/ActionDownload.vue'

import FilesPicker from '../components/FilesPicker.vue';
import logger from '../services/logger.js';
import client from '../services/DavClient.js';
import DavRequest from '../services/DavRequest.js';
import { genFileInfo } from '../utils/fileUtils.js';
import { translate } from '@nextcloud/l10n';
export default {
  name: 'SharedAlbumContent',
  components: {
    MapMarker,
    Plus,
    Close,
    // Download,
    // DownloadMultiple,
    Delete,
    ImagePlus,
    NcEmptyContent,
    NcActions,
    NcActionButton,
    NcActionSeparator,
    NcButton,
    NcModal,
    NcUserBubble,
    CollectionContent,
    // ActionDownload,
    FilesPicker,
    HeaderNavigation
  },
  mixins: [FetchSharedAlbumsMixin, FetchFilesMixin, AbortControllerMixin, isMobile],
  props: {
    albumName: {
      type: String,
      default: '/'
    }
  },

  data() {
    return {
      showAddPhotosModal: false,
      loadingCount: 0,
      loadingAddFilesToAlbum: false
    };
  },

  computed: { ...mapGetters(['files', 'sharedAlbumsFiles']),

    /**
     * @return {object} The album information for the current albumName.
     */
    album() {
      return this.sharedAlbums[this.albumName] || {};
    },

    /**
     * @return {string[]} The list of files for the current albumName.
     */
    albumFileIds() {
      return this.sharedAlbumsFiles[this.albumName] || [];
    },

    /**
     * @return {string} The album name without the userId between parentheses.
     */
    albumOriginalName() {
      return this.albumName.replace(new RegExp(`\\(${this.album.collaborators[0].id}\\)$`), '');
    }

  },
  watch: {
    album() {
      this.fetchAlbumContent();
    }

  },
  methods: { ...mapActions(['appendFiles', 'deleteSharedAlbum', 'addFilesToSharedAlbum', 'removeFilesFromSharedAlbum']),

    async fetchAlbumContent() {
      if (this.loadingFiles || this.showEditAlbumForm) {
        return [];
      }

      const semaphoreSymbol = await this.semaphore.acquire(() => 0, 'fetchFiles');
      const fetchSemaphoreSymbol = await this.fetchSemaphore.acquire();

      try {
        this.errorFetchingFiles = null;
        this.loadingFiles = true;
        this.semaphoreSymbol = semaphoreSymbol;
        const response = await client.getDirectoryContents(`/photos/${getCurrentUser()?.uid}/sharedalbums/${this.albumName}`, {
          data: DavRequest,
          details: true,
          signal: this.abortController.signal
        });
        const fetchedFiles = response.data.map(file => genFileInfo(file));
        const fileIds = fetchedFiles.map(file => file.fileid).map(fileId => fileId.toString());
        this.appendFiles(fetchedFiles);

        if (fetchedFiles.length > 0) {
          await this.$store.commit('addFilesToSharedAlbum', {
            albumName: this.albumName,
            fileIdsToAdd: fileIds
          });
        }

        logger.debug(`[SharedAlbumContent] Fetched ${fileIds.length} new files: `, fileIds);
      } catch (error) {
        if (error.response?.status === 404) {
          this.errorFetchingFiles = 404;
        } else if (error.code === 'ERR_CANCELED') {
          return;
        } else {
          this.errorFetchingFiles = error;
        } // cancelled request, moving on...


        logger.error('[SharedAlbumContent] Error fetching album files', {
          error
        });
      } finally {
        this.loadingFiles = false;
        this.semaphore.release(semaphoreSymbol);
        this.fetchSemaphore.release(fetchSemaphoreSymbol);
      }

      return [];
    },

    async handleFilesPicked(fileIds) {
      this.showAddPhotosModal = false;
      await this.addFilesToSharedAlbum({
        albumName: this.albumName,
        fileIdsToAdd: fileIds
      }); // Re-fetch album content to have the proper filenames.

      await this.fetchAlbumContent();
    },

    async handleRemoveFilesFromAlbum(fileIds) {
      this.$refs.collectionContent.onUncheckFiles(fileIds);
      await this.removeFilesFromSharedAlbum({
        albumName: this.albumName,
        fileIdsToRemove: fileIds
      });
    },

    async handleDeleteAlbum() {
      await this.deleteSharedAlbum({
        albumName: this.albumName
      });
      this.$router.push('/sharedalbums');
    },

    t: translate
  }
};